@use '../variables/colors';
header {
	position: relative;
	z-index: 10;
}
nav {
	background-color: colors.$light-1;
	box-shadow: 0 0 30px #0000001c;
	.nave {
		display: flex;
		grid-template-columns: auto auto auto;
		justify-content: space-between;
		.logo {
			img {
				height: 60px;
			}
			padding: 20px 0;
		}

		ul {
			list-style: none;
			margin: 0;
			height: 100%;
			li {
				position: relative;
				height: 100%;
				width: 100px;
				text-align: center;
				display: inline-block;
				&::after {
					transition: .3s;
					content: "";
					position: absolute;
					left: 0;
					z-index: 1;
					bottom: 0px;
					height: 1px;
					width: 0%;
					background-color: #22a4d9;
				}

				&:hover {
					background-color: colors.$light-0;
					&::after {
						width: 100%;
					}
					a {
						color: colors.$blue-1;
					}
				}
				a {
					display: inline-block;
					position: relative;
					top: 50%;
					transform: translateY(-50%);
				}
			}
		}
	}
}
.divider {
	position: relative;
	img {
		position: absolute;
		width: 100%;
		margin-top: 0px;
		filter: drop-shadow(0px 18px 11px #00000009);
	}
}
